@import url(https://fonts.googleapis.com/css?family=Spicy+Rice);
body {
  margin: 0;
}

.layout {
  display: grid;
  grid-gap: 10px;
  grid-template-columns: repeat(4, 100px);
  grid-auto-rows: 100px;
  background: orange;
  padding: 20px;
}

