.Door {
  display: flex;
  align-items: center;
  justify-content: center;
  background: white;
  color: white;
  border-left: 50px solid crimson;
  border-right: 50px solid crimson;
  border-radius: 20px;
  -webkit-transition: border-width 1s ease;
  transition: border-width 1s ease;
  font-family: 'Spicy Rice', cursive;
  font-size: 20px;
}

.open {
  border-width: 0;
  color: crimson;
}

h1 {
  grid-column-start: 1;
  grid-column-end: -1;
  font-family: 'Spicy Rice', cursive;
}

